import React from 'react'
import './styles/oche_report.css';
import { useState } from 'react';
import { ViewRegisters } from './oche_registers';
import { getNewItemWithId } from '../utils/utils';
import { useDispatch, useSelector } from 'react-redux';
import { SingleSectionPart } from './oche_canevas';
import { copyWith } from '../logic/slice';
import MiniModal from './mini_modal';

export default function OcheReport() {

  const [currentReg, setReg] = useState(null);
  const { registers } = useSelector(state => state.default);
  const dispatch = useDispatch();

  return (
    <div id="oche_report">
      <ViewRegisters onTap={(reg) => { setReg(reg) }} />
      {currentReg && <SummaryList
        register={[...registers].find((reg) => reg.code === currentReg.code)}
        onChange={(sections) => {
          dispatch(copyWith({
            registers: [...registers].map((e) => {
              return e.id !== currentReg?.id ? e : { ...currentReg, reportTables: sections }
            }),
          }));
        }}
      />}
    </div>
  )
}


function SummaryList({ register = {}, onChange = () => { } }) {

  const { reportTables } = register;
  const sections = reportTables ?? [];

  return (
    <div className='summary-list'>
      <div className='oche-inputs-title'>
        <p className='p-medium'>Tables for {register?.code} ({sections?.length})</p>
        <button onClick={(ev) => {
          ev.stopPropagation();
          onChange([...sections, getNewItemWithId({ currentList: sections })])
        }}>Add</button>
      </div>
      {sections.map((e, i) => {
        return <SingleSectionPart title={`Table ${i + 1}`} data={<div className='oche-inputs-item' key={e.id}>
          <p>{e.id}.</p>
          <SingleSection summary={e} onChange={(table) => {
            onChange(sections.map((eachtable) => {
              return eachtable.id !== table.id ? eachtable : { ...eachtable, ...table }
            }))
          }} />
          <i className='modal-header fa fa-xmark' onClick={(ev) => {
            ev.stopPropagation();
            onChange(sections.filter((element) => element.id !== e.id))
          }}></i>
        </div>} />
      })}
    </div>
  )
}

function SingleSection({ summary = {}, onChange = () => { } }) {
  const { languages } = useSelector(state => state.default);
  return <div className='single-oche-input single-section'>
    <div className='input-labeled'>
      <label>Title</label>
      {[...languages].map((lng, i) => {
        const iName = `title_${lng.code}`;
        return <input
          key={i}
          name={iName}
          placeholder={`Title in (${lng.name})`}
          type={'name'}
          value={summary[iName]}
          onChange={(ev) => {
            ev.stopPropagation();
            onChange({ ...summary, [iName]: ev.target.value });
          }}
        />
      })}
    </div>
    <SingleSectionPart title='Columns' data={<SectionColumns columns={summary.cols} onChange={(columns) => {
      onChange({ ...summary, cols: columns });
    }} />} />
    <SingleSectionPart title='Rows' data={<SectionRows cols={summary.cols} rows={summary.rows} onChange={(rws) => {
      onChange({ ...summary, rows: rws });
    }} />} />

  </div>
}


function SectionColumns({ columns = [], onChange = () => { } }) {
  return <div className='options-reg'>
    <div className='oche-title'>
      <label>Columns</label>
      <button onClick={(ev) => {
        ev.preventDefault();
        onChange([...columns, getNewItemWithId({ currentList: columns })]);
      }}>Add</button>
    </div>
    {columns.map((col, i) => {
      return <div key={i} className='options-inputs-row'>
        <p>{i + 1}.</p>
        <SingleColumn item={col} onChangeWith={(newOptionObject) => {
          onChange([...columns.map((element) => {
            return element.id !== col.id ? element : { ...element, ...newOptionObject }
          })])
        }} />
        <i className='modal-header fa fa-xmark' onClick={(ev) => {
          ev.stopPropagation();
          onChange([...columns.filter((element) => element.id !== col.id)]);
        }}></i>
      </div>
    })}
  </div>
}

function SingleColumn({ item = {}, onChangeWith = () => { } }) {

  const { languages } = useSelector(state => state.default);

  const handleChange = (event) => {
    event.preventDefault();
    event.stopPropagation();
    const { name, value } = event.target;
    onChangeWith({ ...item, [name]: value })
  }

  return <form className='opts-single-form' onChange={handleChange}>
    <div className='input-labeled'>
      <label>Title</label>
      {[...languages].map((lng, i) => {
        return <input
          key={i}
          name={`title_${lng.code}`}
          placeholder={`Title in ${lng.name}`}
          type={'name'}
          value={item[`title_${lng.code}`]}
        />
      })}
    </div>
  </form>




}


function SectionRows({ rows = [], cols = [], onChange = () => { } }) {

  const [isMdpd, setMdp] = useState(false);

  return <div className='options-reg'>
    <div className='oche-title'>
      <label>Rows</label>
      <button onClick={(ev) => {
        ev.preventDefault();
        setMdp(true);
      }}>Open</button>
    </div>
    {isMdpd && <MiniModal label="Table Rows Manager" child={<RowSpace rows={rows} cols={cols} onChange={(rows) => {
      onChange(rows);
    }} />} onClose={() => {
      setMdp(false);
    }} />}
  </div>
}


function RowSpace({ cols = [], rows = [], onChange = () => { } }) {

  const { languages } = useSelector(state => state.default);
  const [cLang, setLang] = useState(null);

  return <div className='r-space'>
    <div className='r-space-actions'>
      {languages.length ? <select onChange={(ev) => {
        ev.stopPropagation();
        setLang(ev.target.value);
      }}>
        <option value="">Select language</option>
        {languages.map((e) => {
          return <option key={e.code} value={e.code}>{e.name}</option>
        })}
      </select> : <></>
      }
      <button onClick={(ev) => {
        ev.preventDefault();
        onChange([...rows, { ...getNewItemWithId({ currentList: rows }), cells: cols.map((e, i) => { return { id: i + 1 } }) }]);
      }}>Add row</button>
    </div>
    {cLang?.length && cols?.length ? <table className='r-space-table'>
      <tr>
        <th className='min-width-th-n'>N°</th>
        {cols.map((e, i) => {
          return <th>{e[`title_${cLang}`]}</th>
        })}
        <th className='min-width-th'>Remove</th>
      </tr>
      {rows.map((e, i) => {
        const cells = [...e.cells];
        const isValid = cells.length === cols.length;
        return isValid ? <tr>
          <td className='min-width-th-n'>{i + 1}</td>
          {cells.map((sCell, i) => {
            return <SingleCell
              key={i}
              item={sCell}
              onChange={(newCell) => {
                onChange(
                  rows.map((el) => {
                    return el.id === e.id ? {
                      ...el, cells: cells.map((rCell) => {
                        return rCell.id === newCell.id ? { ...rCell, ...newCell } : rCell;
                      })
                    } : el;
                  })
                )
              }} />
          })}
          <td className='min-width-th'>
            <i className='modal-header fa fa-xmark' onClick={(ev) => {
              ev.stopPropagation();
              onChange(rows.filter((element) => element.id !== e.id))
            }}></i>
          </td>
        </tr> : <></>
      })}
    </table> : <></>}

  </div>
}


function SingleCell({ item = {}, onChange = () => { } }) {
  const { values } = item;
  const cellValues = values ? [...values] : [];
  return <td className='cell-td'>
    <div className='oche-inputs-title'>
      <p className='p-medium'>Cell values</p>
      <button onClick={(ev) => {
        ev.stopPropagation();
        onChange({ ...item, values: [...cellValues, getNewItemWithId({ currentList: cellValues })] })
      }}>Add</button>
    </div>
    {cellValues.map((e, i) => {

      const handleChange = (ev) => {
        ev.stopPropagation();
        const { name, value } = ev.target;
        onChange({
          ...item, values: cellValues.map((cVal) => {
            return cVal.id === e.id ? { ...e, [name]: value } : cVal;
          })
        })
      }

      return <SingleSectionPart
        key={i}
        title={`Cell ${i + 1}`}
        data={<div className='r-space-cell-values-data' key={e.id}>
          <form className='r-space-cell-values-data-form'>
            <div className='oche-languages-form'>
              <div className='input-labeled'>
                <label>Key (Optional)</label>
                <input name='key' type='text' placeholder='DHIS2 Key' value={e.key} onChange={handleChange} />
              </div>
              <div className='input-labeled'>
                <label>Type</label>
                <select name='type' value={e.type} onChange={handleChange}>
                  <option value={null}>Choose</option>
                  <option value="static">Static</option>
                  <option value="count">Count</option>
                  <option value="sum">Sum</option>
                </select>
              </div>
            </div>
            {e?.type === "static" ? <StaticValue item={e} onChange={(data) => {
              console.log("stat d", data);
              onChange({
                ...item, values: cellValues.map((cVal) => {
                  return cVal.id === e.id ? { ...e, "data": data } : cVal;
                })
              })
            }} /> : <></>}
          </form>
          <i className='modal-header fa fa-xmark' onClick={(ev) => {
            ev.stopPropagation();
            onChange({ ...item, values: cellValues.filter((element) => element.id !== e.id) })
          }}></i>
        </div>} />
    })}
  </td>
}


function StaticValue({ item = {}, onChange = () => { } }) {

  const { languages } = useSelector(state => state.default);

  const { data } = item;

  const dataToUSe = data ? { ...data } : {};

  return <div className='input-labeled'>
    <label>Value</label>
    {[...languages].map((lng, i) => {
      const iName = `value_${lng.code}`;
      return <input
        key={i}
        name={iName}
        placeholder={`Value (${lng.name})`}
        type={'name'}
        value={dataToUSe[`value_${lng.code}`]}
        onChange={(ev) => {
          ev.stopPropagation();
          onChange({ ...dataToUSe, [iName]: ev.target.value });
        }}
      />
    })}
  </div>
}


