import React, { Component } from 'react'
import './styles/mini_modal.css';

export default class MiniModal extends Component {
    render() {
        const { child, onClose, label } = this.props;
        return (
            <div className='modal-curtain'>
                <div className='oche-mini-modal'>
                    <div className='mini-modal-header'>
                        <p className='p-medium'>{label ?? ''}</p>
                        <i className='fa fa-xmark' onClick={(ev) => {
                            ev.stopPropagation();
                            onClose();
                        }}></i>
                    </div>
                    <div className='divider' />
                    <div className='modal-child'>{child}</div>
                </div>
            </div>
        )
    }
}
